import React from "react";

import useStyle from "./console.style";

interface Props {        
    messages: string[];
}

export const ConsoleComponent: React.FC<Props> = props => {
  const styles = useStyle();
  const numberOfLines = 10;  
  return <div className={styles.consoleBody}>
        <p>****考古学分類システム****</p>
        <p>システムバーション：0.1</p>                
        {props.messages.slice(Math.max(props.messages.length-numberOfLines,0),props.messages.length).map((t,id) => <p key={`console-message-${id}`}>{t}</p>)}
    </div>  
};