import React from 'react'

import useStyle from "./ribbon.style"
import { Button, Dropdown, DropdownButton } from 'react-bootstrap'
import { IModelInfos } from '../model-infos'

interface Props {
  onClickSend: () => void;
  onUpdateSelection: (model: IModelInfos) => void;
  models: IModelInfos[] | null;
  selectedModel: IModelInfos | null;
  figureName: string | null;
}

const Ribbon: React.FC<Props> = props => {

  // const [selectedModel, setSelectedModel] = useState<IModelInfos|null>(null);

  const styles = useStyle();
  const onSelectDropdown = (selectedModel: IModelInfos) => {    
    props.onUpdateSelection(selectedModel);    
  }

  const onClickSend = (event: any) => {
    props.onClickSend();
  }

  return <div className={styles.ribbonBody}>    
    <div className={styles.title}>
    <label>通信情報</label>
    </div>
    <div className={styles.contents}>
      <div className={styles.figureName}>
      <label>画像名：{props.figureName?props.figureName:"なし"}</label>
      </div>
      <div className={styles.modelSelection}>
      <label>モデルの洗濯：</label>      
    <DropdownButton id="dropdown-basic-button" title={props.selectedModel?props.selectedModel.name:"なし"}>
        {props.models && props.models.map(d => <Dropdown.Item key={`rib-dropdown-${d.name}`} onSelect={()=>onSelectDropdown(d)}>{d.name}</Dropdown.Item>)}      
    </DropdownButton>
    </div>
    <div className={styles.sendButon}>
    <Button onClick={onClickSend}>通信</Button>
    </div>
    </div>
  </div>
}

export default Ribbon;