import { IModelInfos } from "../components/model-infos";
import * as API from "./data-models";

export default class DummyApiClient implements API.IApiClient{
  
  readonly prefix: string;
  readonly hostname: string | null;
  readonly protocol: string | null;

  constructor(hostname: string | null, protocol: "http" | "https" | null) {
    this.prefix = "/api/v1"
    this.hostname = hostname;
    this.protocol = protocol;

    if (hostname && protocol) {
      this.prefix = `${protocol}://${hostname}/api/v1`;
    }
  }

  async ping() {
    return {
        version: "0"
    }
  }

  async getPredictions(image: any) {    
      return {
        messages:["分析を完了しました","結果：",`class1:${Math.floor(Math.random() * 100)}%`,`class2:${Math.floor(Math.random() * 100)}%`,`class3:${Math.floor(Math.random() * 100)}%`],
        extra: {          
          predictions: generateFakePredictions(5),     
          rgbInfo: [generateRGBFakeInfo(),generateRGBFakeInfo(),generateRGBFakeInfo()],
          heatmapURL:"https://i.imgur.com/83XKlAw.jpg"
        }
      }
  }

  async getAvaliableModels() {
    return {messages:["***Connected***","*5 models available"], extra:generateFakeModels(5)};
  }

  async connectToBackend() {

  }
}

function randn_bm(): number {
  let u = 0, v = 0;
  while(u === 0) u = Math.random(); //Converting [0,1) to (0,1)
  while(v === 0) v = Math.random();
  let num = Math.sqrt( -2.0 * Math.log( u ) ) * Math.cos( 2.0 * Math.PI * v );
  num = num / 10.0 + 0.5; // Translate to 0 -> 1
  if (num > 1 || num < 0) return randn_bm(); // resample between 0 and 1
  return num;
}

function generateRGBFakeInfo() {
  // return [...Array(255).keys()].map(() => Math.floor(Math.random() * 100))
  return [...Array(255).keys()].map(() => randn_bm())
}

function generateFakePredictions(quantity: number=3): API.IPredictionUnit[] {
    const randomNumbers = [...Array(quantity).keys()].map(() => Math.floor(Math.random() * 100));
    const normalizer = randomNumbers.reduce((a, b) => a + b, 0)
    const percentages = randomNumbers.map(p => p/normalizer)
    return [...Array(quantity).keys()].map((id: number) => {
        return {
            class: `class-${id}`,
            percentage: percentages[id] * 100
        }
    })
}

function generateFakeModels(numberOfModels=2): IModelInfos[] {
  return [...Array(numberOfModels).keys()].map((d) => ({
    accuracy: Math.floor(Math.random() * 100),
    name: `model-${d}`,
    numberOfClasses: Math.floor(Math.random() * 3),
    trainSize: Math.floor(Math.random() * 1500 + 300),
    testSize: Math.floor(Math.random() * 300),
    version: "1.0-alpha"
  }))
}