import { HorizontalBar } from 'react-chartjs-2';
import { IPredictionUnit } from '../../services';

import useStyle from "./chart.style";

interface Props {
  predictions: IPredictionUnit[]
}

const BarplotComponent = (props:Props) => {

    const styles = useStyle();

    const data = {
        labels: props.predictions.map(e => e.class),
        datasets: [
          {
            label: '確率',
            data: props.predictions.map(e => Math.round(e.percentage)),
            backgroundColor: [
              'rgba(129, 44, 74, 1)',
              'rgba(252, 254, 223, 1)',
              'rgba(160, 142, 129, 1)',
              'rgba(61, 105, 124, 1)',              
              'rgba(249, 158, 92, 1)',
              'rgba(101, 161, 143, 1)',
              'rgba(252, 227, 156, 1)',              
              'rgba(90, 130, 138, 1)',
              'rgba(39, 57, 59, 1)',
            ],
            borderWidth: 1,
          },
        ],
      }

      const options = {
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
              },
            },
          ],
          xAxes: [
              {
                  ticks: {
                      beginAtZero: true,
                      max: 100.0
                  }
              }
          ]
        },
      }

    return <div className={styles.chartBody}>
       <HorizontalBar data={data} options={options} />
        </div>
    
}

export default BarplotComponent;