import React from "react";

import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";


import useStyle from "./left-bar.style";

import * as H from "history";

export interface Props {
  history: H.History;
  screenPaths: { classification: string;
    classificationBeta: string;
     manual:string; about: string; project: string;};
}

export const MainMenu: React.FC<Props> = props => {
  return (
    <ButtonGroup vertical>
      <MenuButton
        history={props.history}
        to={props.screenPaths.classification}
        text="分類"
        // icon={faTh}
      />
      <MenuButton
        history={props.history}
        to={props.screenPaths.classificationBeta}
        text="分類2"
        // icon={faRobot}
      />
      <MenuButton
        history={props.history}
        to={props.screenPaths.manual}
        text="使い方"
        // icon={faRobot}
      />
            <MenuButton
        history={props.history}
        to={props.screenPaths.project}
        text="Project"
        // icon={faRobot}
      />
      <MenuButton
        history={props.history}
        to={props.screenPaths.about}
        text="About Us"
        // icon={faRobot}
      />
    </ButtonGroup>
  );
};

interface MenuButtonProps {
  history: H.History;
  to: string;
  text: string;
//   icon: IconDefinition;
}

const MenuButton: React.FC<MenuButtonProps> = props => {
  const isSelected = props.history.location.pathname === props.to;

  const styles = useStyle();
  return (
    <Button
      variant={isSelected ? "primary" : "secondary"}
      onClick={() => props.history.push(props.to)}
      className={styles.btn}
    >
      {props.text}
      {/* <FontAwesomeIcon icon={props.icon}></FontAwesomeIcon> */}
    </Button>
  );
};