import React, { useContext, useState } from "react";
import {Upload} from "../../components/upload"
import {ImageComponent} from "../../components/image/image"
import ChartComponent from "../../components/charts/barplot";
import DoughnutComponent from "../../components/charts/doughnut"
import LineComponent from "../../components/charts/line";
import RGBComponent from "../../components/charts/rgb";

import {ConsoleComponent} from "../../components/console";
import Ribbon from "../../components/ribbons/ribbon";

import {IModelInfos} from "../../components/model-infos"

import * as ClassView from "./view";
import ModelInfos from "../../components/model-infos";

import AppContext from "../../app-context";
import { IPredictionUnit } from "../../services";
import NoData from "../../components/no-data";

interface Props {

}

const defaultState = {data: [], layout: {
  xaxis:{
      range:[-800,2000]
    },
    yaxis:{
      range:[0,1200]
    }, frames: [], config: {}}}

export const ClassificationViewBeta: React.FC<Props> = props => {

  const context = useContext(AppContext);

  const [isUploading, setIsUploading] = useState<boolean>(false);
  const [imageTmpURL, setImageTmpURL] = useState<any>(null);
  const [imageFile, setImageFile] = useState<any>(null);
  const [selectedModel, setSelectedModel] = useState<IModelInfos|null>(null);
  const [selectedImageName, setSelectedImageName] = useState<string|null>(null);
  const [modelsList, setModelsList] = useState<IModelInfos[]|null>(null);
  const [consoleMessages, setConsoleMessages] = useState<string[]>(["Connecting..."]);  
  const [rgbInfo, setRGBInfo] = useState<number[][]>([[...Array(255)].map(()=>0),[...Array(255)].map(()=>0),[...Array(255)].map(()=>0)]);
  const [predictions, setPredictions] = useState<IPredictionUnit[]>([]);
  const [imState, setState] = useState<any>(defaultState);
  const [heatmapImageURL, setHeatmapImageURL] = useState<string|null>(null);
  

  if (!context.apiClient) {return <div/>}

  if (!modelsList) { getModelsListFromBackend();}

  async function getModelsListFromBackend(){
    if (!context.apiClient) { return; }

    const answer = await context.apiClient.getAvaliableModels();
    // TODO: Fix this hack! Maybe useEffect
    if(consoleMessages.length === 1) {logToConsole(answer.messages);}
    
    setModelsList(answer.extra);
    setSelectedModel(answer.extra[0]);
  }
  
  const onImageChange = (e: any) => {
    setState(e)
    // console.log("RANGES!!!!!",ranges)
    // ranges = ranges
    // setRanges(ranges)
  }

  const onImageUpload = (e: any) => {
    const file = Array.from(e.target.files)[0] as any;    
    const imageContent = URL.createObjectURL(file);
    setSelectedImageName(file.name);
    setIsUploading(true);
    setImageTmpURL(imageContent);
    setIsUploading(false);
    setImageFile(file);
    setState({...imState,  layout: {
              xaxis:{
                  range:[-800,2000]
                },
                yaxis:{
                  range:[0,1200]
                },images:[
                  {
                    x: 0,
                    y: 1200,
                    sizex: 1500,
                    sizey: 1200,
                    sizing:"stretch",
                    source: imageContent,
                    xanchor: "left",
                    yanchor:"top",
                    xref: "x",                
                    yref: "y",
                    // opacity:0.4,
                    layer:"below"
                  }
                ]  }})
  }

  // onImageZoomChanged = ()

  const onClickSend = async () => {    
    // console.log(imState);
    // const xaxis = imState.layout.xaxis.range
    // const yaxis = imState.layout.yaxis.range
    // const img = imState.layout.images[0]
    // console.log([img.x,img.sizex,0,img.sizey])
    // console.log([...xaxis,...yaxis]);
    // return;
    if (!context.apiClient) {logToConsole(["バックエンドと繋がれませんでした"]);return;}
    if(!imageTmpURL) {alert("画像を洗濯してください"); return;}
    console.log("送った")
    logToConsole(["画像を送りました","しばらくお待ちください"]);    
    const response = await context.apiClient.getPredictions(imageFile);
    console.log(response.extra.predictions);
    logToConsole(response.messages);
    setRGBInfo(response.extra.rgbInfo);
    setPredictions(response.extra.predictions);
    setHeatmapImageURL(response.extra.heatmapURL);

  }

  function logToConsole(messages:string[]) {
    setConsoleMessages([...consoleMessages, ...messages])
  }

  const onChangeSelectedModel = (model: IModelInfos) => {    
    setSelectedModel(model);
  }

  return <ClassView.ThreeColumnsLayout>
      <ClassView.FirstColumn>
      <ClassView.Title>
              <label>分類された画像</label>
            </ClassView.Title>
        <ClassView.FirstColumnDisplay>          
        <ImageComponent imState={imState} onUpdateState={onImageChange} />
        </ClassView.FirstColumnDisplay>
        <ClassView.FirstColumnRibbon>
          <Ribbon models={modelsList} selectedModel={selectedModel} onUpdateSelection={onChangeSelectedModel} figureName={selectedImageName?selectedImageName:"なし"} onClickSend={onClickSend}/>
        </ClassView.FirstColumnRibbon>
        <ClassView.FirstColumnBottom>
        
          <ClassView.FirstColumnBottomLeft>
            <ClassView.Title>
              <label>バープロット</label>
            </ClassView.Title>
            {predictions.length<=0?<NoData/>:<ChartComponent predictions={predictions}/>}
          </ClassView.FirstColumnBottomLeft>
          <ClassView.FirstColumnBottomRight>
          <ClassView.Title>
              <label>ドーナツ</label>
            </ClassView.Title>
          {predictions.length<=0?<NoData/>:<DoughnutComponent predictions={predictions}/>}
          </ClassView.FirstColumnBottomRight>
        </ClassView.FirstColumnBottom>
      </ClassView.FirstColumn>      
      <ClassView.SecondColumn>
         <ClassView.Title>
              <label>ヒートマップ</label>
            </ClassView.Title>
        <ClassView.SecondColumnTopDisplay>          
        {heatmapImageURL?<img width="100%" alt={"データが無し!"} src={heatmapImageURL}/>:<NoData/>}
        </ClassView.SecondColumnTopDisplay>
        <ClassView.Title>
              <label>洗濯したモデルの情報</label>
        </ClassView.Title>
        <ClassView.SecondColumnMiddleDisplay>
        <ModelInfos modelInfos={selectedModel} />
        
        </ClassView.SecondColumnMiddleDisplay>
        <ClassView.Title>
              <label>RGB</label>
        </ClassView.Title>
        <ClassView.SecondColumnBottomDisplay>
          {/* <LineComponent rgbInfo={rgbInfo}/> */}
          {predictions.length<=0?<NoData/>:<RGBComponent rgbInfo={rgbInfo}/>}
        </ClassView.SecondColumnBottomDisplay>
      </ClassView.SecondColumn>
      <ClassView.ThirdColumn>
        <ClassView.Title>
            <label>アップロード</label>
        </ClassView.Title>
        <ClassView.ThirdColumnTop>          
          <Upload onChange={onImageUpload} isUploading={isUploading}/>
        </ClassView.ThirdColumnTop>
        <ClassView.Title>
            <label>コンソール</label>
        </ClassView.Title>
        <ClassView.ThirdColumnBot>
          <ConsoleComponent messages={consoleMessages} />
        </ClassView.ThirdColumnBot>
      </ClassView.ThirdColumn>
  </ClassView.ThreeColumnsLayout>
  
};