import { Doughnut } from 'react-chartjs-2';
import { IPredictionUnit } from '../../services';

import useStyle from "./chart.style";

interface Props {
  predictions: IPredictionUnit[]
}
 
const DoughnutComponent = (props:Props) => {

    const styles = useStyle();

    const data = {
        // labels: ['粘土・繊維', '粘土のみ', '粘土・礫'],
        labels: props.predictions.map(e => e.class),
        datasets: [
          {
            label: '確率',
            data: props.predictions.map(e => Math.round(e.percentage)),
            backgroundColor: [
              'rgba(129, 44, 74, 1)',
              'rgba(252, 254, 223, 1)',
              'rgba(160, 142, 129, 1)',
              'rgba(61, 105, 124, 1)',
              
              'rgba(249, 158, 92, 1)',
              'rgba(101, 161, 143, 1)',
              'rgba(252, 227, 156, 1)',              
              'rgba(90, 130, 138, 1)',
              'rgba(39, 57, 59, 1)',
            ],
            // borderColor: [
            //   'rgba(255, 99, 132, 1)',
            //   'rgba(54, 162, 235, 1)',
            //   'rgba(255, 206, 86, 1)',              
            // ],
            borderWidth: 1,
          },
        ],
      }

      const options = {}

    return <div className={styles.chartBody}>
       <Doughnut data={data} options={options} />
        </div>
    
}

export default DoughnutComponent;