import React, { useEffect, useState } from 'react'
import useStyle from "./image.style";

import Plot from "react-plotly.js"

export interface Props {
    // image: any;
    onUpdateState: (figure: any) => void;
    imState: any;
}

export const ImageComponentNew: React.FC<Props> = props => {  
  const styles = useStyle();  
  
    return <Plot        
        data={props.imState.data}
        layout={props.imState.layout}          
        frames={props.imState.frames}
        config={props.imState.config}
        onInitialized={(figure)=>props.onUpdateState(figure)} 
        onUpdate={(figure:any) => props.onUpdateState(figure)}
           />      
  };

