import useStyle from "./general.style"

interface Props {
    name:string;
    pictureComponent?:JSX.Element;    
    title: string;
    department: string;
    description:string;
}

const ProfileCard: React.FC<Props> = props => {
    const styles = useStyle();
  return <>
   <Card className="text-center" style={{ width: '25rem' }}>
      
      <Card.Body>
      <Card.Title>{props.name}</Card.Title>
      {props.pictureComponent?props.pictureComponent:<FontAwesomeIcon icon={faUser} size='5x' color='#3B5998' />}
      <Card.Text>
        {props.description}
      </Card.Text>
    </Card.Body>
    <Card.Footer>
      <Card.Text>
      {props.title}
      </Card.Text>
      <Card.Text>
      {props.department}
      </Card.Text>
        
    </Card.Footer>
      </Card>
      </>
};

export default ProfileCard;