import React from "react";

import useStyle from "./model-infos.style";

export interface IModelInfos {
    name: string;
    version: string;
    testSize: number;
    trainSize: number;
    accuracy: number;
    numberOfClasses: number;
}

interface Props {
    modelInfos: IModelInfos | null;    
}

export const ModelInfos: React.FC<Props> = props => {
  const styles = useStyle();
  return <div className={styles.body}>
        <label>Name : {props.modelInfos && props.modelInfos.name}</label><br/>
        <label>Version : {props.modelInfos && props.modelInfos.version}</label><br/>
        <label>TrainSize : {props.modelInfos && props.modelInfos.trainSize}</label><br/>
        <label>TestSize : {props.modelInfos && props.modelInfos.testSize}</label><br/>          
        <label>Accuracy : {props.modelInfos && props.modelInfos.accuracy}%</label><br/>
        <label>#of classes : {props.modelInfos && props.modelInfos.numberOfClasses}</label>
    </div>  
};

export default ModelInfos;