import React from "react";
import {
  HashRouter as Router,
  Route,
  RouteComponentProps,
} from "react-router-dom";


import * as TwoCols from "../layout/sidebar-layout";

import { MainMenu } from "../containers/leftBar";
import { ClassificationView2Cols } from "../containers/classification2Col"
import {ClassificationViewBeta} from "../containers/classification"
import { AboutView } from "../containers/about"

import Header from "../components/header";

import ProjectContainer from "../containers/project/project";
import ManualContainer from "../containers/manual/manual";

const MainScreenPaths = {
  classification: "/classification",
  classificationBeta: "/classification-beta",
  about: "/about",
  project:"/project",
  manual: "/manual"
};

const Routes: React.FC = () => (
  <Router>
      <TwoCols.TwoColumnsLayout>
        <TwoCols.Title>
            <TwoCols.Header>
              <Header/>
            </TwoCols.Header>      
        </TwoCols.Title>
      <TwoCols.TwoColumnsContent>    
        <TwoCols.FirstColumn>
            <Route path="/" component={MainMenuWrapper} />
        </TwoCols.FirstColumn>
        <TwoCols.SecondColumn>
            {/* <Route path={MainScreenPaths.classification} component={ClassificationView} /> */}
            <Route path={MainScreenPaths.classificationBeta} component={ClassificationViewBeta} />
            <Route path={MainScreenPaths.classification} component={ClassificationView2Cols} />
            
            <Route path={MainScreenPaths.manual} component={ManualContainer} />
            <Route path={MainScreenPaths.project} component={ProjectContainer} />
            <Route path={MainScreenPaths.about} component={AboutView} />
            
        </TwoCols.SecondColumn>
      </TwoCols.TwoColumnsContent>
    </TwoCols.TwoColumnsLayout>
  </Router>
);

const MainMenuWrapper: React.FC<RouteComponentProps> = (props) => (
  <MainMenu history={props.history} screenPaths={MainScreenPaths} />
);

export default Routes;

