import React from "react";

import useStyle from "./general.style"

interface Props {
    lines: string[];
    title?: string;
}

const TextComponent: React.FC<Props> = props => {
const styles = useStyle();
  return <div>      
      <div className={styles.body}>
      {props.lines.map((t,id) => <p key={`console-message-${id}`}>{id+1}: {t}</p>)}
      </div>    
    </div>
};

export default TextComponent;